<template>
  <div>
    <div class="inputButton">
      <el-input class="input"  placeholder="请输入内容" v-model="inputContent" clearable></el-input>
      <el-button class="button"  type="primary">搜索</el-button>
      <el-button class="button" type="primary" @click="dialogFormVisible = true">新建</el-button>
      <el-button class="button" type="primary" @click="testLink">测试</el-button>
    </div>

    <el-tabs value="first" type="border-card" @tab-click="handleClick" style="margin-top: 15px;">
      <el-tab-pane label="待响应" name="first">
        <el-table border :data="waitTableData" style="width: 99%;margin-top: 15px;">
          <el-table-column type="index" :index="indexMethod" label="序号" width="60px" align="center" ></el-table-column>
          <el-table-column prop="device_id" label="登记号" min-width="12%" align="center"></el-table-column>
          <el-table-column prop="username" label="客户姓名" min-width="7%" align="center"></el-table-column>
          <el-table-column prop="address" label="地址" min-width="15%" align="center"></el-table-column>
          <el-table-column prop="phone" label="联系方式" min-width="10%" align="center"></el-table-column>
          <el-table-column prop="faultSever" label="维修人员" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="severPhone" label="联系方式" min-width="10%" align="center"></el-table-column>
          <el-table-column prop="state" label="状态" min-width="6%" align="center"></el-table-column>
          <el-table-column label="操作" min-width="8%" align="center">
            <template slot-scope="scope">
              <router-link to="/fault/userFaultDetail" class="routelink">
                <el-button type="primary" style="padding:10px 15px 10px 15px;"
                           @click="userFaultDetail(scope.$index, scope.row)">详情
                </el-button>
              </router-link>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>

      <el-tab-pane label="维修中" name="second">
        <el-table border :data="ongoingTableData" style="width: 98%;margin-top: 15px;">
          <el-table-column type="index" label="序号" width="60px" align="center" ></el-table-column>
          <el-table-column prop="device_id" label="登记号" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="username" label="客户姓名" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="address" label="地址" min-width="15%" align="center"></el-table-column>
          <el-table-column prop="phone" label="联系方式" min-width="10%" align="center"></el-table-column>
          <el-table-column prop="faultSever" label="维修人员" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="severPhone" label="联系方式" min-width="10%" align="center"></el-table-column>
          <el-table-column prop="state" label="状态" min-width="6%" align="center">
            <template slot-scope="scope">
              <span v-if="scope.row.state === 1" style="color: green;font-size: large;">维修中</span>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>

      <el-tab-pane label="已完成" name="third">
        <el-table border :data="finishTableData" style="width: 98%;margin-top: 15px;">
          <el-table-column type="index" label="序号" width="60px" align="center" ></el-table-column>
          <el-table-column prop="device_id" label="登记号" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="username" label="客户姓名" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="address" label="地址" min-width="15%" align="center"></el-table-column>
          <el-table-column prop="phone" label="联系方式" min-width="10%" align="center"></el-table-column>
          <el-table-column prop="faultSever" label="维修人员" min-width="8%" align="center"></el-table-column>
          <el-table-column prop="severPhone" label="联系方式" min-width="10%" align="center"></el-table-column>
          <el-table-column prop="state" label="状态" min-width="6%" align="center">
            <template slot-scope="scope">
              <span v-if="scope.row.state === 1">成功</span>
              <span v-else style="color: red">失败</span>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="所有用户" name="fourth"></el-tab-pane>
    </el-tabs>
    <el-dialog title="故障记录信息" :visible.sync="dialogFormVisible">
      <el-form :label-position="labelPosition" label-width="80px">
        <el-form-item label="设备号">
         <el-input v-model="form.device_id" autocomplete="on"></el-input>
        </el-form-item>
        <el-form-item label="户主姓名">
          <el-input v-model="form.username" autocomplete="on"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="故障现象">
          <el-input v-model="form.faultPhenomenon" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="派修">
          <span>张三</span>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page"
                :limit.sync="listQuery.limit" @pagination="getWaitFaultRecords"
                style="text-align: center"
    />
  </div>
</template>

<script>
import selectServer from './selectServrer'
import { test2 } from '../../api/fault.js'
import pagination from '@/components/Pagination'
import axios from 'axios'
export default {
  name: 'faultRecords',
  components: {
    selectServer,
    pagination
  },
  data () {
    return {
      inputContent: '',
      listLoading: false,
      tempList: [],
      waitTableData: [],
      ongoingTableData: [],
      finishTableData: [],
      total: 0,
      listQuery: {
        page: 1,
        limit: 10,
        username: undefined,
        mobile: undefined,
        sort: 'add_time',
        order: 'desc'
      },
      dialogFormVisible: false,
      labelPosition: 'right',
      form: {
        device_id: null,
        username: '',
        address: '',
        phone: '',
        faultPhenomenon: ''
      }
    }
  },
  computed: {
  },
  created: function () {
    // console.log('created')
    this.getWaitFaultRecords()
  },
  mounted: function () {
    // console.log('mounted')
  },
  activated: function () {
    // console.log('activated')
    this.getOngoingFaultRecords()
  },
  methods: {
    testLink: function () {
      test2().then(rep => {
        if (!rep.code) {
          console.log(rep)
        } else {
          alert('fail')
        }
      })
    },
    indexMethod (index) {
      return ((this.listQuery.page - 1) * this.listQuery.limit + index + 1)
    },
    handleClick (tab, event) {
      // console.log(tab.index, event)
      if (tab.index === '0') {
        this.getWaitFaultRecords()
      }
      if (tab.index === '1') {
        this.getOngoingFaultRecords()
      } else {
        this.getFinishFaultRecords()
      }
    },
    userFaultDetail (index, row) {
      console.log(index, row)
    },
    getWaitFaultRecords () {
      axios.get('../../../static/admin/faultRecords.json').then(res => {
        res = res.data
        if (res.ret && res.data) {
          const data = res.data
          this.tempList = data.waitRecordList
          this.total = data.waitRecordList.length
          var i = 0
          this.waitTableData = []
          for (i = (this.listQuery.page - 1) * this.listQuery.limit; i < this.listQuery.page * this.listQuery.limit; i++) {
            // console.log(i)
            this.waitTableData.push(data.waitRecordList[i])
          }
        }
      })
    },
    getOngoingFaultRecords () {
      this.total = 0
      axios.get('../../../static/admin/faultRecords.json').then( res => {
        res = res.data
        if (res.ret && res.data) {
          const data = res.data
          this.ongoingTableData = data.ongoingRecordList
        }
      })
    },
    getFinishFaultRecords () {
      this.total = 0
      axios.get('../../../static/admin/faultRecords.json').then( res => {
        res = res.data
        if (res.ret && res.data) {
          const data = res.data
          this.finishTableData = data.finishRecordList
        }
      })
    }
  }
}
</script>

<style scoped>
.inputButton{
  display: flex;
  margin-top: 10px;
}
.input{
    width: 300px;
}
.button{
  margin-left:30px;
}
</style>



